// app/page.tsx
'use client'

import React, { useState, useRef, useCallback, useEffect } from 'react';
import { Upload, Download, FileVideo, Loader2, CheckCircle2, XCircle, Play, Trash2, Copy, Sun, Moon, Globe, Zap, Server, Settings, Languages } from 'lucide-react';
import { validateVideoFile } from '@/lib/validation';
import { textToSRT, textToSRTWithTimestamps } from '@/lib/exportSRT';
import AISolarTranscriber from '@/components/AISolarTranscriber';

import AISolarTranscriber from '@/components/AISolarTranscriber'



interface HistoryItem {
  id: string;
  name: string;
  text: string;
  date: string;
  language?: string;
  engine?: string;
}

export default function Page() {
  return (
    <main className="space-y-6">
      <AISolarTranscriber />
    </main>
  )
}

export default function AISolarTranscriber() {
  const [file, setFile] = useState<File | null>(null);
  const [videoPreview, setVideoPreview] = useState<string>('');
  const [transcript, setTranscript] = useState('');
  const [progress, setProgress] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState(false);
  const [darkMode, setDarkMode] = useState(true);
  const [isDragging, setIsDragging] = useState(false);
  const [showSettings, setShowSettings] = useState(false);
  const [history, setHistory] = useState<HistoryItem[]>([]);
  
  // Settings
  const [engine, setEngine] = useState<'openai' | 'local'>('openai');
  const [language, setLanguage] = useState('auto');
  const [autoTranslate, setAutoTranslate] = useState(false);
  const [targetLanguage, setTargetLanguage] = useState('ru');
  const [enableSegmentation, setEnableSegmentation] = useState(true);
  const [enableSpeakers, setEnableSpeakers] = useState(false);
  
  const fileInputRef = useRef<HTMLInputElement>(null);
  const videoRef = useRef<HTMLVideoElement>(null);

  useEffect(() => {
    const saved = localStorage.getItem('aisolar-history');
    if (saved) {
      try {
        setHistory(JSON.parse(saved));
      } catch (e) {
        console.error('Failed to load history', e);
      }
    }
  }, []);

  const saveToHistory = useCallback((name: string, text: string) => {
    const newEntry: HistoryItem = {
      id: Date.now().toString(),
      name,
      text,
      date: new Date().toISOString(),
      language,
      engine
    };
    const updated = [newEntry, ...history].slice(0, 10);
    setHistory(updated);
    localStorage.setItem('aisolar-history', JSON.stringify(updated));
  }, [history, language, engine]);

  const handleDragOver = useCallback((e: React.DragEvent) => {
    e.preventDefault();
    setIsDragging(true);
  }, []);

  const handleDragLeave = useCallback((e: React.DragEvent) => {
    e.preventDefault();
    setIsDragging(false);
  }, []);

  const handleDrop = useCallback((e: React.DragEvent) => {
    e.preventDefault();
    setIsDragging(false);
    
    const droppedFile = e.dataTransfer.files[0];
    if (droppedFile) {
      handleFileSelect(droppedFile);
    }
  }, []);

  const handleFileSelect = (selectedFile: File) => {
    const validation = validateVideoFile(selectedFile);
    
    if (!validation.valid) {
      setError(validation.error || 'Неверный файл');
      setFile(null);
      setVideoPreview('');
      return;
    }
    
    setFile(selectedFile);
    setError('');
    setSuccess(false);
    setTranscript('');
    
    const url = URL.createObjectURL(selectedFile);
    setVideoPreview(url);
  };

  const handleFileInput = (e: React.ChangeEvent<HTMLInputElement>) => {
    const selectedFile = e.target.files?.[0];
    if (selectedFile) {
      handleFileSelect(selectedFile);
    }
  };

  const startTranscription = async () => {
    if (!file) return;

    setLoading(true);
    setError('');
    setSuccess(false);
    setProgress('Подготовка...');

    try {
      const formData = new FormData();
      formData.append('file', file);
      formData.append('engine', engine);
      formData.append('language', language);
      formData.append('enableSegmentation', String(enableSegmentation));
      formData.append('enableSpeakers', String(enableSpeakers));
      formData.append('autoTranslate', String(autoTranslate));
      if (autoTranslate && targetLanguage) {
        formData.append('targetLanguage', targetLanguage);
      }

      const response = await fetch('/api/transcribe', {
        method: 'POST',
        body: formData
      });

      if (!response.ok) {
        if (response.status === 429) {
          const data = await response.json();
          throw new Error(data.message || 'Превышен лимит запросов');
        }
        throw new Error(`HTTP ${response.status}`);
      }

      const reader = response.body?.getReader();
      const decoder = new TextDecoder();
      let finalText = '';

      if (reader) {
        while (true) {
          const { value, done } = await reader.read();
          if (done) break;

          const chunk = decoder.decode(value, { stream: true });
          const lines = chunk.split('\n').filter(Boolean);

          for (const line of lines) {
            try {
              const data = JSON.parse(line);
              
              if (data.type === 'progress') {
                setProgress(data.message);
              } else if (data.type === 'partial') {
                setTranscript(data.text);
              } else if (data.type === 'final') {
                finalText = data.text;
                setTranscript(data.text);
              } else if (data.type === 'error') {
                throw new Error(data.message);
              }
            } catch (e) {
              if (e instanceof Error && !e.message.includes('JSON')) {
                throw e;
              }
            }
          }
        }
      }

      setSuccess(true);
      setProgress('Транскрибирование завершено! ✓');
      
      if (finalText) {
        saveToHistory(file.name, finalText);
      }
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Произошла ошибка');
      setProgress('');
    } finally {
      setLoading(false);
    }
  };

  const downloadTxt = () => {
    const blob = new Blob([transcript], { type: 'text/plain;charset=utf-8' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `transcript_${Date.now()}.txt`;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  const downloadJson = () => {
    const data = {
      filename: file?.name,
      timestamp: new Date().toISOString(),
      transcript: transcript,
      settings: {
        engine,
        language,
        autoTranslate,
        targetLanguage,
        enableSegmentation,
        enableSpeakers
      },
      metadata: {
        fileSize: file?.size,
        fileType: file?.type
      }
    };
    const blob = new Blob([JSON.stringify(data, null, 2)], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `transcript_${Date.now()}.json`;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  const downloadSRT = () => {
    const srtContent = enableSegmentation 
      ? textToSRTWithTimestamps(transcript)
      : textToSRT(transcript, 4);
    
    const blob = new Blob([srtContent], { type: 'text/plain;charset=utf-8' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `subtitles_${Date.now()}.srt`;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  const copyToClipboard = () => {
    navigator.clipboard.writeText(transcript);
    alert('Текст скопирован в буфер обмена!');
  };

  const clearAll = () => {
    setFile(null);
    setVideoPreview('');
    setTranscript('');
    setProgress('');
    setError('');
    setSuccess(false);
    if (fileInputRef.current) {
      fileInputRef.current.value = '';
    }
  };

  const loadFromHistory = (item: HistoryItem) => {
    setTranscript(item.text);
    setSuccess(true);
    setProgress(`Загружено из истории: ${item.name}`);
  };

  const deleteHistoryItem = (id: string) => {
    const updated = history.filter(h => h.id !== id);
    setHistory(updated);
    localStorage.setItem('aisolar-history', JSON.stringify(updated));
  };

  return (
    <div className={`min-h-screen transition-colors duration-300 ${darkMode ? 'bg-gradient-to-br from-slate-900 via-purple-900 to-slate-900' : 'bg-gradient-to-br from-blue-50 via-purple-50 to-pink-50'}`}>
      <div className="max-w-7xl mx-auto p-6 space-y-8">
        {/* Header */}
        <div className="flex items-center justify-between">
          <div>
            <h1 className={`text-4xl font-bold ${darkMode ? 'text-white' : 'text-gray-900'} flex items-center gap-3`}>
              <span className="text-5xl">🌞</span>
              AISOLAR
              <span className={`text-sm px-3 py-1 rounded-full ${darkMode ? 'bg-purple-500/20 text-purple-300' : 'bg-purple-100 text-purple-700'}`}>
                v2.0
              </span>
            </h1>
            <p className={`mt-2 ${darkMode ? 'text-purple-300' : 'text-purple-600'}`}>
              AI-транскрибация с поддержкой многоязычности и умной сегментации
            </p>
          </div>
          <div className="flex gap-2">
            <button
              onClick={() => setShowSettings(!showSettings)}
              className={`p-3 rounded-xl transition-all ${darkMode ? 'bg-white/10 hover:bg-white/20 text-purple-300' : 'bg-gray-200 hover:bg-gray-300 text-gray-700'}`}
            >
              <Settings size={24} />
            </button>
            <button
              onClick={() => setDarkMode(!darkMode)}
              className={`p-3 rounded-xl transition-all ${darkMode ? 'bg-white/10 hover:bg-white/20 text-yellow-300' : 'bg-gray-200 hover:bg-gray-300 text-gray-700'}`}
            >
              {darkMode ? <Sun size={24} /> : <Moon size={24} />}
            </button>
          </div>
        </div>

        {/* Settings Panel */}
        {showSettings && (
          <div className={`rounded-2xl p-6 ${darkMode ? 'bg-white/5 border border-white/10' : 'bg-white shadow-lg'}`}>
            <h3 className={`text-lg font-semibold mb-4 ${darkMode ? 'text-white' : 'text-gray-900'}`}>
              Настройки транскрибации
            </h3>
            
            <div className="grid md:grid-cols-2 gap-6">
              <div>
                <label className={`block text-sm font-medium mb-2 ${darkMode ? 'text-gray-300' : 'text-gray-700'}`}>
                  Движок распознавания
                </label>
                <div className="flex gap-3">
                  <button
                    onClick={() => setEngine('openai')}
                    className={`flex-1 p-3 rounded-xl transition-all ${
                      engine === 'openai'
                        ? darkMode ? 'bg-purple-500 text-white' : 'bg-purple-600 text-white'
                        : darkMode ? 'bg-white/5 hover:bg-white/10 text-gray-300' : 'bg-gray-100 hover:bg-gray-200 text-gray-700'
                    }`}
                  >
                    <Zap className="inline mr-2" size={18} />
                    OpenAI
                  </button>
                  <button
                    onClick={() => setEngine('local')}
                    className={`flex-1 p-3 rounded-xl transition-all ${
                      engine === 'local'
                        ? darkMode ? 'bg-purple-500 text-white' : 'bg-purple-600 text-white'
                        : darkMode ? 'bg-white/5 hover:bg-white/10 text-gray-300' : 'bg-gray-100 hover:bg-gray-200 text-gray-700'
                    }`}
                  >
                    <Server className="inline mr-2" size={18} />
                    Local
                  </button>
                </div>
              </div>

              <div>
                <label className={`block text-sm font-medium mb-2 ${darkMode ? 'text-gray-300' : 'text-gray-700'}`}>
                  <Globe className="inline mr-1" size={16} />
                  Язык аудио
                </label>
                <select
                  value={language}
                  onChange={(e) => setLanguage(e.target.value)}
                  className={`w-full p-3 rounded-xl ${darkMode ? 'bg-white/5 text-white' : 'bg-gray-100 text-gray-900'} outline-none`}
                >
                  <option value="auto">Авто-определение</option>
                  <option value="ru">Русский</option>
                  <option value="en">English</option>
                  <option value="uk">Українська</option>
                  <option value="pl">Polski</option>
                  <option value="de">Deutsch</option>
                  <option value="fr">Français</option>
                  <option value="es">Español</option>
                </select>
              </div>

              <div>
                <label className="flex items-center gap-3 cursor-pointer">
                  <input
                    type="checkbox"
                    checked={autoTranslate}
                    onChange={(e) => setAutoTranslate(e.target.checked)}
                    className="w-5 h-5 rounded accent-purple-500"
                  />
                  <span className={`text-sm font-medium ${darkMode ? 'text-gray-300' : 'text-gray-700'}`}>
                    <Languages className="inline mr-1" size={16} />
                    Автоматический перевод
                  </span>
                </label>
                {autoTranslate && (
                  <select
                    value={targetLanguage}
                    onChange={(e) => setTargetLanguage(e.target.value)}
                    className={`w-full mt-2 p-2 rounded-lg ${darkMode ? 'bg-white/5 text-white' : 'bg-gray-100 text-gray-900'} outline-none text-sm`}
                  >
                    <option value="ru">→ Русский</option>
                    <option value="en">→ English</option>
                    <option value="uk">→ Українська</option>
                    <option value="pl">→ Polski</option>
                  </select>
                )}
              </div>

              <div className="space-y-3">
                <label className="flex items-center gap-3 cursor-pointer">
                  <input
                    type="checkbox"
                    checked={enableSegmentation}
                    onChange={(e) => setEnableSegmentation(e.target.checked)}
                    className="w-5 h-5 rounded accent-purple-500"
                  />
                  <span className={`text-sm font-medium ${darkMode ? 'text-gray-300' : 'text-gray-700'}`}>
                    Умная сегментация по паузам
                  </span>
                </label>
                <label className="flex items-center gap-3 cursor-pointer">
                  <input
                    type="checkbox"
                    checked={enableSpeakers}
                    onChange={(e) => setEnableSpeakers(e.target.checked)}
                    className="w-5 h-5 rounded accent-purple-500"
                  />
                  <span className={`text-sm font-medium ${darkMode ? 'text-gray-300' : 'text-gray-700'}`}>
                    Определение спикеров
                  </span>
                </label>
              </div>
            </div>
          </div>
        )}

        <div className="grid lg:grid-cols-3 gap-6">
          <div className="lg:col-span-2 space-y-6">
            {/* Upload Zone */}
            <div
              onDragOver={handleDragOver}
              onDragLeave={handleDragLeave}
              onDrop={handleDrop}
              className={`relative rounded-2xl border-2 border-dashed transition-all duration-300 ${
                isDragging
                  ? darkMode ? 'border-purple-400 bg-purple-500/20' : 'border-purple-500 bg-purple-100'
                  : darkMode ? 'border-purple-500/50 bg-white/5' : 'border-gray-300 bg-white'
              } ${darkMode ? 'hover:border-purple-400 hover:bg-white/10' : 'hover:border-purple-400 hover:bg-purple-50'} p-12 text-center cursor-pointer`}
              onClick={() => fileInputRef.current?.click()}
            >
              <input
                ref={fileInputRef}
                type="file"
                accept="video/*,.avi"
                onChange={handleFileInput}
                className="hidden"
              />
              
              <div className="space-y-4">
                <div className={`mx-auto w-20 h-20 rounded-full flex items-center justify-center ${darkMode ? 'bg-purple-500/20' : 'bg-purple-100'}`}>
                  {file ? <FileVideo className={darkMode ? 'text-purple-300' : 'text-purple-600'} size={40} /> : <Upload className={darkMode ? 'text-purple-300' : 'text-purple-600'} size={40} />}
                </div>
                
                <div>
                  <p className={`text-xl font-semibold ${darkMode ? 'text-white' : 'text-gray-900'}`}>
                    {file ? file.name : 'Перетащите видео или нажмите для выбора'}
                  </p>
                  <p className={`mt-2 text-sm ${darkMode ? 'text-gray-400' : 'text-gray-600'}`}>
                    Поддержка: AVI, MP4, MOV, MKV • До 500MB
                  </p>
                  {file && (
                    <div className={`mt-3 space-y-1 text-sm ${darkMode ? 'text-purple-300' : 'text-purple-600'}`}>
                      <p>Размер: {(file.size / 1024 / 1024).toFixed(2)} MB</p>
                      <p>Формат: {file.type || 'video'}</p>
                    </div>
                  )}
                </div>
              </div>
            </div>

            {videoPreview && (
              <div className={`rounded-2xl overflow-hidden ${darkMode ? 'bg-white/5' : 'bg-white'} p-4`}>
                <video
                  ref={videoRef}
                  src={videoPreview}
                  controls
                  className="w-full rounded-xl"
                  style={{ maxHeight: '300px' }}
                />
              </div>
            )}

            {file && (
              <div className="flex gap-3 flex-wrap">
                <button
                  onClick={startTranscription}
                  disabled={loading}
                  className={`flex-1 min-w-[200px] px-6 py-4 rounded-xl font-semibold transition-all transform hover:scale-105 ${
                    loading
                      ? darkMode ? 'bg-gray-600 cursor-not-allowed' : 'bg-gray-300 cursor-not-allowed'
                      : darkMode ? 'bg-gradient-to-r from-purple-500 to-pink-500 hover:from-purple-600 hover:to-pink-600 text-white shadow-lg shadow-purple-500/50' : 'bg-gradient-to-r from-purple-600 to-pink-600 hover:from-purple-700 hover:to-pink-700 text-white shadow-lg'
                  }`}
                >
                  {loading ? (
                    <>
                      <Loader2 className="inline animate-spin mr-2" size={20} />
                      Обработка...
                    </>
                  ) : (
                    <>
                      <Play className="inline mr-2" size={20} />
                      Транскрибировать
                    </>
                  )}
                </button>

                <button
                  onClick={clearAll}
                  disabled={loading}
                  className={`px-6 py-4 rounded-xl font-semibold transition-all ${
                    darkMode ? 'bg-red-500/20 hover:bg-red-500/30 text-red-300' : 'bg-red-100 hover:bg-red-200 text-red-700'
                  }`}
                >
                  <Trash2 className="inline mr-2" size={20} />
                  Очистить
                </button>
              </div>
            )}

            {progress && (
              <div className={`rounded-xl p-4 ${darkMode ? 'bg-blue-500/20 border border-blue-500/30' : 'bg-blue-50 border border-blue-200'}`}>
                <div className="flex items-center gap-3">
                  {loading && <Loader2 className={`animate-spin ${darkMode ? 'text-blue-300' : 'text-blue-600'}`} size={20} />}
                  {success && <CheckCircle2 className="text-green-500" size={20} />}
                  <p className={`${darkMode ? 'text-blue-200' : 'text-blue-800'}`}>{progress}</p>
                </div>
                {loading && (
                  <div className={`mt-3 h-2 ${darkMode ? 'bg-white/10' : 'bg-gray-200'} rounded-full overflow-hidden`}>
                    <div className="h-full bg-gradient-to-r from-purple-500 to-pink-500 animate-pulse" style={{ width: '100%' }} />
                  </div>
                )}
              </div>
            )}

            {error && (
              <div className={`rounded-xl p-4 ${darkMode ? 'bg-red-500/20 border border-red-500/30' : 'bg-red-50 border border-red-200'}`}>
                <div className="flex items-center gap-3">
                  <XCircle className="text-red-500" size={20} />
                  <p className={`${darkMode ? 'text-red-200' : 'text-red-800'}`}>{error}</p>
                </div>
              </div>
            )}

            {transcript && (
              <div className={`rounded-2xl overflow-hidden ${darkMode ? 'bg-white/5' : 'bg-white shadow-lg'}`}>
                <div className={`p-4 flex items-center justify-between ${darkMode ? 'bg-white/10' : 'bg-gray-100'}`}>
                  <h3 className={`font-semibold ${darkMode ? 'text-white' : 'text-gray-900'}`}>
                    Результат транскрибации
                  </h3>
                  <div className="flex gap-2">
                    <button onClick={copyToClipboard} className={`p-2 rounded-lg transition-all ${darkMode ? 'bg-white/10 hover:bg-white/20 text-purple-300' : 'bg-purple-100 hover:bg-purple-200 text-purple-700'}`} title="Копировать">
                      <Copy size={18} />
                    </button>
                    <button onClick={downloadTxt} className={`px-3 py-2 rounded-lg text-sm transition-all ${darkMode ? 'bg-white/10 hover:bg-white/20 text-green-300' : 'bg-green-100 hover:bg-green-200 text-green-700'}`}>
                      TXT
                    </button>
                    <button onClick={downloadJson} className={`px-3 py-2 rounded-lg text-sm transition-all ${darkMode ? 'bg-white/10 hover:bg-white/20 text-blue-300' : 'bg-blue-100 hover:bg-blue-200 text-blue-700'}`}>
                      JSON
                    </button>
                    <button onClick={downloadSRT} className={`px-3 py-2 rounded-lg text-sm transition-all ${darkMode ? 'bg-white/10 hover:bg-white/20 text-orange-300' : 'bg-orange-100 hover:bg-orange-200 text-orange-700'}`}>
                      SRT
                    </button>
                  </div>
                </div>
                <textarea
                  value={transcript}
                  onChange={(e) => setTranscript(e.target.value)}
                  className={`w-full h-96 p-6 resize-none font-mono text-sm leading-relaxed outline-none ${
                    darkMode ? 'bg-slate-900/50 text-gray-100' : 'bg-white text-gray-900'
                  }`}
                  placeholder="Транскрибированный текст появится здесь..."
                />
                <div className={`p-3 text-xs flex justify-between ${darkMode ? 'text-gray-400' : 'text-gray-600'}`}>
                  <span>{transcript.length} символов · {transcript.split(/\s+/).filter(Boolean).length} слов</span>
                  <span>Язык: {language === 'auto' ? 'Авто' : language.toUpperCase()} · {engine === 'openai' ? 'OpenAI' : 'Local'}</span>
                </div>
              </div>
            )}
          </div>

          <div className="space-y-6">
            <div className={`rounded-2xl p-6 ${darkMode ? 'bg-white/5' : 'bg-white shadow-lg'}`}>
              <h3 className={`text-lg font-semibold mb-4 ${darkMode ? 'text-white' : 'text-gray-900'}`}>
                История
              </h3>
              
              {history.length === 0 ? (
                <p className={`text-sm ${darkMode ? 'text-gray-400' : 'text-gray-600'}`}>
                  История пуста
                </p>
              ) : (
                <div className="space-y-3 max-h-[600px] overflow-y-auto">
                  {history.map((item) => (
                    <div
                      key={item.id}
                      className={`p-3 rounded-xl cursor-pointer transition-all ${
                        darkMode ? 'bg-white/5 hover:bg-white/10' : 'bg-gray-50 hover:bg-gray-100'
                      }`}
                    >
                      <div className="flex items-start justify-between gap-2">
                        <div className="flex-1 min-w-0" onClick={() => loadFromHistory(item)}>
                          <p className={`text-sm font-medium truncate ${darkMode ? 'text-white' : 'text-gray-900'}`}>
                            {item.name}
                          </p>
                          <p className={`text-xs mt-1 ${darkMode ? 'text-gray-400' : 'text-gray-600'}`}>
                            {new Date(item.date).toLocaleDateString('ru-RU', {
                              day: 'numeric',
                              month: 'short',
                              hour: '2-digit',
                              minute: '2-digit'
                            })}
                          </p>
                          <p className={`text-xs mt-2 line-clamp-2 ${darkMode ? 'text-gray-500' : 'text-gray-700'}`}>
                            {item.text}
                          </p>
                        </div>
                        <button
                          onClick={(e) => {
                            e.stopPropagation();
                            deleteHistoryItem(item.id);
                          }}
                          className={`p-1 rounded transition-colors ${darkMode ? 'hover:bg-red-500/20 text-red-400' : 'hover:bg-red-100 text-red-600'}`}
                        >
                          <Trash2 size={14} />
                        </button>
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </div>
          </div>
        </div>

        <div className={`text-center py-6 border-t ${darkMode ? 'border-white/10 text-gray-400' : 'border-gray-200 text-gray-600'}`}>
          <p className="text-sm">
            AISOLAR v2.0 · Powered by Next.js 14 + OpenAI + FFmpeg
          </p>
        </div>
      </div>
    </div>
  );
}